import React, { useState } from 'react';
import chapters from '../chapters.json';

import AnimatedCursor from 'react-animated-cursor';
import { Chapters, MegaMenu, SideMenu } from './';
import { Routes, Route } from 'react-router-dom';
import Home from '../layouts/home';
import FullBook from '../layouts/fullBook';

function App() {
  const [chapter, setChapter] = useState(1);
  const [open, setOpen] = useState(false);
  const target = React.createRef();

  return (
    <>
      <AnimatedCursor
        outerSize={16}
        innerSize={4}
        innerScale={0.8}
        outerScale={3}
        color='193, 11, 111'
      />
      <SideMenu
        chapters={chapters}
        chapter={chapter}
        handleOpen={(s) => setOpen(s)}
        handleChapter={(ch) => setChapter(ch)}
      />
      <div className='content' ref={target}>
        <Routes>
          <Route path='/' component={Home} exact />
          <Route path='/fulltext' component={FullBook} exact />
          {chapters.map((item, index) => (
            <Route
              key={index}
              path={`/chapters/:name`}
              render={(props) => (
                <Chapters
                  item={item}
                  next={chapters[index + 1]}
                  previous={index > 0 ? chapters[index - 1] : chapters[8]}
                />
              )}
              exact
            />
          ))}
        </Routes>
      </div>

      <MegaMenu
        chapters={chapters}
        handleOpen={() => setOpen(false)}
        open={open}
        handleChapter={(ch) => setChapter(ch)}
      />
    </>
  );
}

export default App;
